.sidebarContainer{
    position: fixed;
    display: flex;
    height: 100vh;
    width: 150px;
    flex-direction: column;
    background-color: rgb(75,50,130);
    box-shadow: 5px 0 5px 0 rgba(0,0,0,0.4);
    z-index: 12;
}
.closeIcon{
    position: relative;
    color: white;
    float: right;
}
.closeDiv{
}
.closeIcon:hover{
    color: rgba(255,255,255,0.6);
}
.opcion{
    height: 10vh;
    background-color: transparent;
    color: white;
    width: 150px;
}
.opcion:hover{
    background-color: rgb(105,80,160);
}
.subtitulo{
    position: relative;
    margin: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: rgb(75,50,130);
    padding: 0 10px;
    width: fit-content;
    color: white;
}
.subtituloContainer{
    position: relative;
    text-align: center;
    height: 10vh;
}
.divisor{
    position: relative;
    height: 1px;
    background-color: white;
    top: 5.5vh;
    margin: 0px 5px;
}