.archivosContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 20px;
    max-width: 1400px;
    margin: 0 auto;
}
.archivosContainer::after {
    content: "";
    width: 300px;
}
.archivo{
    width: 100%;
    max-width: 300px;
    height: 180px;
    object-fit: cover;
    margin:0;
    border-radius: 5px 5px 0 0;
    border: 1px solid gray;
    border-bottom: none;
}
.iframeBottom{
    height: 70px;
    background-color: rgb(75,50,130);
    margin-top: -5px;
    border-radius: 0 0 5px 5px;
    max-width: 300px;
}
.displayTitle{
    position: relative;
    text-align: center;
    color: white;
    margin: 0;
    cursor: pointer;
    font-weight: bold;
    left: 50%;
    transform: translate(-50%,0);
}

.like:hover{
    background-color: lightgray;
}

.like:disabled{
    background-color: gray;
}