
.alert {
    position: relative;
    top: 0;
    display: none;
    padding: 20px;
    color: white;
    margin-bottom: 15px;
    z-index: 30;
    animation-name: apareciendo;
    animation-duration: 0.5s;
}

@keyframes apareciendo {
    from{
        top:-40px
    }
    to{
        top:0
    }
}


/* The close button */
.closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
}

/* When moving the mouse over the close button */
.closebtn:hover {
    color: black;
}